import { useState, useRef } from '@wordpress/element';
import { __ } from '@wordpress/i18n';
import { InspectorControls, BlockControls, AlignmentToolbar } from '@wordpress/block-editor';
import { PanelBody, PanelRow, TextControl, SelectControl, __experimentalUnitControl as UnitControl, Dashicon } from '@wordpress/components';

// Settings Components
import Title from '../../Components/Title';
import { InlineDetailMediaUpload, MediaEditControl } from '../../Components/MediaControl';
import { pxUnit, perUnit, emUnit, vhUnit } from '../../Components/Helper/options';

import { debounce } from './utils/functions';
import { embedModes } from './utils/options';
import SettingsSaveForm from './SettingsSaveForm';

const Settings = ({ attributes, setAttributes, data, dataLoading, saveData, isLoading }) => {
	const { file, config, alignment, width, height } = attributes;
	const { embedMode = '' } = config;

	const [title, setTitle] = useState(file.title);

	const onConfigChange = (type, val) => setAttributes({ config: { ...config, [type]: val } });

	const updateTitle = useRef(debounce((existingFile, text) => {
		setAttributes({ file: { ...existingFile, title: text } });
	}, 750)).current;

	return <>
		<InspectorControls>
			<PanelBody className='bPlPanelBody help' title={__('Help', 'pdf-embed-block')} initialOpen={false}>
				<div className='helpItem'>
					<a href='https://bblockswp.com/docs/pdf-embed-block/' target='_blank' rel='noreferrer'><Dashicon icon='book' />{__('Read Documentation', 'pdf-embed-block')}</a>
				</div>

				<div className='helpItem rateUs'>
					<a href='https://wordpress.org/support/plugin/pdf-embed-block/reviews/#new-post' target='_blank' rel='noreferrer'>
						<span><Dashicon icon='star-filled' />{__('Would you please rate us?', 'pdf-embed-block')}</span>
						<span>{__('We are new and we need your help to grow!🙏', 'pdf-embed-block')}</span>
					</a>
				</div>
			</PanelBody>


			<PanelBody className='bPlPanelBody' title={__('PDF Settings', 'pdf-embed-block')}>
				<SettingsSaveForm data={data} dataLoading={dataLoading} saveData={saveData} isLoading={isLoading} />

				<Title>{__('PDF File:', 'pdf-embed-block')}</Title>
				<InlineDetailMediaUpload value={file} types={['application/pdf']} onChange={val => {
					setTitle(val.title);
					setAttributes({ file: val })
				}} placeholder={__('Enter PDF URL', 'pdf-embed-block')} />

				<Title className='mt10'>{__('PDF File Name:', 'pdf-embed-block')}</Title>
				<TextControl value={title} onChange={val => {
					setTitle(val);
					updateTitle(file, val);
				}} />

				<PanelRow>
					<Title className=''>{__('Embed Mode:', 'pdf-embed-block')}</Title>
					<SelectControl value={embedMode} onChange={val => onConfigChange('embedMode', val)} options={embedModes} />
				</PanelRow>
			</PanelBody>


			<PanelBody className='bPlPanelBody' title={__('Layout Settings', 'pdf-embed-block')} initialOpen={false}>
				<UnitControl label={__('Width:', 'pdf-embed-block')} labelPosition='left' value={width} onChange={val => setAttributes({ width: val })} units={[pxUnit(650), perUnit(100), emUnit(40)]} isResetValueOnUnitChange={true} />

				{'IN_LINE' !== embedMode && <UnitControl className='mt10' label={__('Height:', 'pdf-embed-block')} labelPosition='left' value={height} onChange={val => setAttributes({ height: val })} units={[pxUnit(459), vhUnit(100), emUnit(28)]} isResetValueOnUnitChange={true} />}
			</PanelBody>
		</InspectorControls>


		<BlockControls>
			<MediaEditControl label={__('Edit PDF:', 'pdf-embed-block')} icon='pdf' types={['application/pdf']} value={file} onChange={val => setAttributes({ file: val })} />

			<AlignmentToolbar value={alignment} onChange={val => setAttributes({ alignment: val })} describedBy={__('PDF Embed Alignment')} alignmentControls={[
				{ title: __('PDF Embed in left', 'pdf-embed-block'), align: 'left', icon: 'align-left' },
				{ title: __('PDF Embed in center', 'pdf-embed-block'), align: 'center', icon: 'align-center' },
				{ title: __('PDF Embed in right', 'pdf-embed-block'), align: 'right', icon: 'align-right' }
			]} />
		</BlockControls>
	</>;
};
export default Settings;